body{
  background-image: linear-gradient(to right bottom, #00F260, #0575E6);
  background-attachment: fixed;
  color: #354538;
}
a{
  text-decoration: none;
  color: #354548;
}
a:hover{
  text-decoration: none;
  color: black;
}
.app_container{
  padding-top: 50px;
}
.app_row{
  height: 86vh;
} 
.app_main-content{
  background-color: #fff;
  border-radius: 20px;
  padding: 25px 15px;
}

/* sidebar */
.sidebar{
  background-color: #fff;
  height: 100%;
  border-radius: 20px; 
  padding: 25px 15px;
  box-shadow: 0 0 10px black;
  text-align: center;
}
.sidebar_avatar{
  width: 100px;
  height: 100px;
  border-radius: 100px;
  object-fit: contain;
}
.sidebar_icon{
  width: 25px;
  height: 25px;
}
.sidebar_name{
  font-family: 'Poppins';
  margin: 20px 0 ;
  font-size: 1.7rem;
}
.sidebar_name>span{
  color: #00F260;
}
.sidebar_item{
margin-bottom: 6px;
padding-top: 6px;
height: 40px;
font-size: 1.05rem;
border-radius: 30px;
}
.sidebar_title{
  background-color: #f1f1f1;
  border-radius: 30px;
}
.sidebar_contact{
  background-color: #dbdddf;
  padding: 2px 0;

margin: 20px -15px;
}
.sidebar_email{
 background-image: linear-gradient(to right bottom, #00F260, #0575E6);
 border-radius: 30px;
 color: #fff;
 margin: 15px 25px 0px;
 cursor: pointer;
 transition: all 3s linear;
}
.sidebar_email:hover{
  transform: scale(1.2);
}   
.sidebar_github:hover,
.sidebar_resume:hover{
  background-color: #f1f1f2;
  color: #000;
}

/* media query */

  @media(max-width:991px){
     .app_main-content{
       margin: 36px 0;
     }
  }


/* navbar styles */
.navbar_items{
  display: flex;

}
.navbar_active{
  font-size: 2rem;
  font-weight: 700;
  color: #354358;
  border-bottom: 4px solid #00F260;
}
.navbar_item{
  margin-right: 20px;
  text-decoration: none;
  color: #354358;
  font-size: 1.3rem;
  cursor: pointer;
}
.navbar_item:hover{
  background: -webkit-linear-gradient(#00F260, #0575E6);
  background-clip: text;
  -webkit-background-clip : text;
  -webkit-text-fill-color: transparent;
}  

/* about */
.about{
  padding: 30px;
  margin: -35px;
}
.about_intro{
  font-size: 1.1rem;
  padding: 20px;
  margin: 10px 0;
}
.about_container{
  background-color: #dbdddf;
  padding: 30px 20px;
}
.about_heading{
  font-size: 25px;
  font-weight: 700;
  color: #354358;
  margin-left: 10px;
}
 /* skill-card */
.skill-card{
  background-color: #fff;
  margin: 10px;
  padding: 10px;
  display: flex;
  flex-direction: row;
  border-radius: 10px;
}
.skill-card_icon{
  width: 40px;
  height: 40px;
} 
.skill-card-body{
  margin-left: 15px;
}
.skill-card_xontent{
  margin: 0;
  font-size: 0.9rem;
  color: #6e7e91;
}

/* resume */
.resume{
  padding-top: 20px;
}
.resume-card_heading,
.resume-language_heading{
  margin-left: 10px;
  font-synthesis: 1.8rem;
  font-weight: 700;
  color: #354538;
}

.resume-card_body{
  margin-top: 10px;
}
.resume-card_title{
  font-weight: 700;
}
.resume-card_name{
  font-size: 16px;
}
.resume-card_details{
  font-size: 0.9rem;
  padding-top: 20px;
}


/* bar */
.bar{
  width: 90%;
  background-color: #f1f2f3;
  border-radius: 30px;
  margin-bottom: 12px;
}
.bar_icon{
  height: 15px;
  width: 15px;
}
.bar_wrapper{
  background-image: linear-gradient( 90deg, #00F260, #0575E6);
  border-radius: 20px;
  padding: 4px 14px;
  color: #fff;
  font-size: 0.9rem; 
}